ara y se expresa poniendo barreras; al abordarlo, se debe tener seguridad de lo que se le va a manifestar y ofrecer disponibilidad para atenderlo cuando lo desee.")])])]),_c('div',{staticClass:"cajon color-acento-botones punto-caliente-second-color px-5 py-4",attrs:{"x":"13%","y":"96%","numero":"+"}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Este tipo de cliente es discreto, cauto, serio, egoísta, callado y distante; para abordarlo, es necesario recurrir a la objetividad, aportando siempre argumentos sólidos, enfatizando en aspectos claves, planteando preguntas directas, con paciencia, e indagando acerca de lo que le gusta y mostrarle empatía.")])])]),_c('div',{staticClass:"cajon color-acento-botones punto-caliente-second-color px-5 py-4",attrs:{"x":"41%","y":"96%","numero":"+"}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Este tipo de cliente es seguro, de posiciones firmes, se mantiene informado, razón por la que puede ser tajante y directo con sus respuestas; al abordarlo, se debe hacerlo con seguridad, planteando argumentos y llevando evidencias de los mismos, es decir, utilizar muestras si es un producto tangible, o testimonio si se comercializan productos intangibles, prestando atención a sus planteamientos, dedicando el tiempo necesario para puntualizar y concretar la venta, sin llevarle la contraria o entrar en divergencias.")])])]),_c('div',{staticClass:"cajon color-acento-botones punto-caliente-second-color px-5 py-4",attrs:{"x":"70%","y":"96%","numero":"+"}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Este tipo de cliente es indeciso, tímido, reservado y parco para abordarlo; hay que mostrarle empatía, ofrecerle colaboración, presentar el producto o servicios sin presión ni afán, inducir su decisión de compra mediante el profesionalismo y ganarse la confianza de su acompañante.")])])])])],1),_c('p',{staticClass:"mt-5 mb-5"},[_vm._v("De esta manera y con el objetivo de trabajar