ente que todo cliente anhela recibir. Ambos se definen como la fórmula encargada de otorgar las herramientas necesarias para proyectar una imagen exitosa, positiva y segura; tanto en el área laboral como en la personal.")]),_vm._m(9),_c('p',{staticClass:"mt-5 mb-5"},[_vm._v("A continuación, se detalla cada una de estas reglas:")]),_c('div',{staticClass:"row justify-content-center mb-5 mt-5"},[_c('div',{staticClass:"col-md-10"},[_c('AcordionA',{staticClass:"mt-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta-acordeon-verde"}},[_c('div',{staticClass:"row bg2 p-5",attrs:{"titulo":"Organización "}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Mantener una zona de trabajo despejada y ordenada, ayudará a cumplir con los retos diarios y beneficiará el trabajo cotidiano. Demostrar que se es una persona organizada, muy seguramente beneficiará su ocupación diaria y le permitirá entregar oportunamente sus productos, como resultado de llevar un proceso ordenado, previendo errores y que corresponda con los requerimientos asignados. El método en el lugar de trabajo, también denota orden, limpieza e higiene.")])])]),_c('div',{staticClass:"bg2 p-5",attrs:{"titulo":"Paciencia"}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Adoptar esta virtud o este valor, implica tener un comportamiento moderado y pertinente en toda ocasión, lo que capacitará para brindar un trato adecuado a todo tipo de cliente que posea una organización.")])])]),_c('div',{staticClass:"bg2 p-5",attrs:{"titulo":"Prudencia"}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Es el más importante de todos los principios que rigen, tanto la etiqueta como el protocolo; es muy sencilla de aplicar y tiene que ver con mantener una actitud de respeto hacia el cliente en la comunicación, indiferente del medio que se utilice; se trata de expresar opiniones solo si son solicitadas, de cuidar lo que se expresa verbal y gestualmente, para no afectar a ot